function isWin(matrix, row, colum, val) {
  let n = matrix.length;
  let count = 0;
  // 
  for (let j = colum; j < n; j++) {
    if (matrix[row][j] === val) {
      count++;
      if (count === 5) {
        return true;
      }
    } else {
      break;
    }
  }
  count = 0;
  // 
  for (let i = row; i < n; i++) {
    if (matrix[i][colum] === val) {
      count++;
      if (count === 5) {
        return true;
      }
    } else {
      break;
    }
  }
  count = 0;
  // 
  for (let i = row, j = colum; i < n && j >= 0; i++, j--) {
    if (matrix[i][j] === val) {
      count++;
      if (count === 5) {
        return true;
      }
    } else {
      break;
    }
  }
  count = 0;
  // 
  for (let i = row, j = colum; i < n && j < n; i++, j++) {
    if (matrix[i][j] === val) {
      count++;
      if (count === 5) {
        return true;
      }
    } else {
      break;
    }
  }
  // 5
  return false;
}

function alertWinner(matrix) {
  let winner = "";
  /* */
  for (let i = 0; i < 19; i++) {
    for (let j = 0; j < 19; j++) {
      let val = matrix[i][j];
      if (val !== 0) {
        if (isWin(matrix, i, j, val)) {
          winner = val === 1 ? "" : "";
          return winner;
        }
      }
    }
  }
  return winner;
}

export function getTime() {
  let time = new Date();
  let hour = time.getHours();
  let minute = time.getMinutes();
  let second = time.getSeconds();
  hour = hour < 10 ? "0" + hour : hour;
  minute = minute < 10 ? "0" + minute : minute;
  second = second < 10 ? "0" + second : second;
  return `${hour}:${minute}:${second}`;
}

// 
function evaluate(board, row, col, player) {
  let value = 0;
  let c1 = 1,
    c2 = 1,
    c3 = 1,
    c4 = 1;
  // 
  // 
  for (let j = col + 1; j < board.length; j++) {
    if (board[row][j] === player) {
      c1++;
    } else {
      break;
    }
  }
  for (let j = col - 1; j >= 0; j--) {
    if (board[row][j] === player) {
      c1++;
    } else {
      break;
    }
  }

  // 
  for (let i = row + 1; i < board.length; i++) {
    if (board[i][col] === player) {
      c2++;
    } else {
      break;
    }
  }
  for (let i = row - 1; i >= 0; i--) {
    if (board[i][col] === player) {
      c2++;
    } else {
      break;
    }
  }
  // 45*
  for (let i = row + 1, j = col - 1; i < board.length && j >= 0; i++, j--) {
    if (board[i][j] === player) {
      c3++;
    } else {
      break;
    }
  }
  for (let i = row - 1, j = col + 1; i >= 0 && j < board.length; i--, j++) {
    if (board[i][j] === player) {
      c3++;
    } else {
      break;
    }
  }
  // -45*
  for (let i = row + 1, j = col + 1; i < board.length && j < board.length; i++, j++) {
    if (board[i][j] === player) {
      c4++;
    } else {
      break;
    }
  }
  for (let i = row - 1, j = col - 1; i >= 0 && j >= 0; i--, j--) {
    if (board[i][j] === player) {
      c4++;
    } else {
      break;
    }
  }

  value += Math.max(c1, c2, c3, c4);
  return value;
}

// 
function autoPlacePiece(board, player) {
  let maxValue = -Infinity;
  let maxRow = 0;
  let maxCol = 0;

  // 
  const rows = board.length;
  const cols = board[0].length;

  for (let i = 0; i < rows; i++) {
    for (let j = 0; j < cols; j++) {
      if (board[i][j] === 0) {
        const value = evaluate(board, i, j, player);
        if (value > maxValue) {
          maxValue = value;
          maxRow = i;
          maxCol = j;
        }
      }
    }
  }

  return [maxValue, maxRow, maxCol];
}

// 
export function autoPlay(board, player) {
  const opponent = player === 1 ? 2 : 1;
  const autoPlayValue = autoPlacePiece(board, player); // 
  const autoDefendValue = autoPlacePiece(board, opponent); // 
  let maxRow = -1;
  let maxCol = -1;

  // 
  if (autoPlayValue[0] >= autoDefendValue[0]) {
    maxRow = autoPlayValue[1];
    maxCol = autoPlayValue[2];
  } else {
    maxRow = autoDefendValue[1];
    maxCol = autoDefendValue[2];
  }

  return [maxRow, maxCol];
}

export default alertWinner;
